.About h1 {
  font-size: 50px;
  margin-top: 6%;
  margin-bottom: 3%;  
  text-shadow: 0 2px rgb(109, 155, 173);
  font-weight: 800;
  color: #54849b;
}
  
.About h2 {
  font-size: 25px;
}
  
.About h1:focus, h1:hover {
  animation: wobble 1.5s 1;
}
  
@keyframes wobble {
  25% {
      transform: rotate(0.5deg);
  }
  50% {
      transform: rotate(-1deg);
  }
  75% {
      transform: rotate(0.5deg);
  }
  100% {
      transform: rotate(0deg);
  }
}
  
.About a img:hover {
    opacity: 0.7;
}

.cow {
  position: relative;
  margin: auto;
  display: block;
  margin: 1% 38% 1% 38%;
  width: 50vmin;
  height: 40vmin;
  transition: all 1s;
}

.cow:hover {
  transform: scale(1.1);
}  


.horns {
  position: absolute;
  left: 10vmin;
  top: -9vmin;
  width: 30vmin;
  height: 20vmin;
  border-radius: 80%;
  box-shadow: 4vmin 4vmin 0 #ffe9e1, -4vmin 4vmin 0 #ffe9e1;
}

.head {
  position: absolute;
  left: 10vmin;
  top: 5vmin;
  width: 30vmin;
  height: 32vmin;
  background: white;
  border-radius: 70% 70% 40% 40%;
  overflow: hidden;
}

.head::before {
  content: '';
  position: absolute;
  width: 15vmin;
  height: 16vmin;
  background: rgb(46, 45, 45);
  border-radius: 40% 50% 40% 40%;
  transform: rotate(60deg);
}

.nose {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 25vmin;
  height: 17vmin;
  background: pink;
  border-radius: 50% 50% 30% 30%;
  bottom: 0;
}

.nose::before {
  content: '';
  left: 6vmin;
  top: 6vmin;
  position: absolute;
  width: 2vmin;
  height: 4vmin;
  border-radius: 50%;
  background: rgb(158, 110, 110);
}

.nose::after {
  content: '';
  right: 6vmin;
  top: 6vmin;
  position: absolute;
  width: 2vmin;
  height: 4vmin;
  border-radius: 50%;
  background: rgb(158, 110, 110);
}

.mouth {
  position: absolute;
  bottom: 1vmin;
  left: 50%;
  transform: translateX(-50%);
  width: 20vmin;
  height: 5vmin;
  border-radius: 50%;
  box-shadow: 0 0.75vmin 0 rgb(230, 174, 184);
}

.eye {
  width: 4vmin;
  height: 4vmin;
  position: absolute;
  border: 2px solid rgb(29, 28, 28);
  border-radius: 50%;
  top: 10vmin;
  background: white;
}

.eye::before {
  width: 2vmin;
  height: 2vmin;
  content: '';
  position: absolute;
  background: black;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
}

.eye.left {
  left: 6vmin;
}

.eye.right {
  right: 6vmin;
}


@media (max-width: 770px) {
    .About h1 {
      font-size: 30px;
      margin-top: 30%;
      margin-bottom: 20%;    
    }
    .cow {
      margin: 0 25%;
    }
    
    .About h2 {
      font-size: 20px;
      margin-top: 10%;
    }
}